var queryString = require('querystring');
var REQUEST = require('../lib/request');
var util = require('./util');


// Bucket 

/**
 * Bucket
 * @param  {Object}  params                     
 *     @param  {String}  params.Bucket          Bucket
 *     @param  {String}  params.Region          
 * @param  {Function}  callback                 
 * @return  {Object}  err                       
 * @return  {Object}  data                      
 *     @return  {Boolean}  data.BucketExist     Bucket
 *     @return  {Boolean}  data.BucketAuth       Bucket 
 */
function headBucket(params, callback) {
    submitRequest.call(this, {
        Bucket: params.Bucket,
        Region: params.Region,
        AppId: params.AppId,
        method: 'HEAD',
    }, function (err, data) {
        var exist, auth, statusCode;
        if (err) {
            statusCode = err.statusCode;
            if (statusCode && statusCode === 404) {
                exist = false;
                auth = false;
            } else if (statusCode && statusCode === 403) {
                exist = true;
                auth = false;
            } else {
                return callback(err);
            }
        } else {
            statusCode = data.statusCode;
            exist = true;
            auth = true;
        }
        var result = {
            BucketExist: exist,
            BucketAuth: auth,
            statusCode: statusCode
        };
        if (data && data.headers) {
            result.headers = data.headers;
        }
        callback(null, result);
    });
}

/**
 *  Bucket  object 
 * @param  {Object}  params                         
 *     @param  {String}  params.Bucket              Bucket
 *     @param  {String}  params.Region              
 *     @param  {String}  params.Prefix              
 *     @param  {String}  params.Delimiter           PrefixPrefixdelimiter
 *     @param  {String}  params.Marker              UTF-8marker
 *     @param  {String}  params.MaxKeys             1000
 *     @param  {String}  params.EncodingType        
 * @param  {Function}  callback                     
 * @return  {Object}  err                           
 * @return  {Object}  data                          
 *     @return  {Object}  data.ListBucketResult      object 
 */
function getBucket(params, callback) {
    var reqParams = {};
    reqParams['prefix'] = params['Prefix'];
    reqParams['delimiter'] = params['Delimiter'];
    reqParams['marker'] = params['Marker'];
    reqParams['max-keys'] = params['MaxKeys'];
    reqParams['encoding-type'] = params['EncodingType'];

    submitRequest.call(this, {
        method: 'GET',
        Bucket: params.Bucket,
        Region: params.Region,
        AppId: params.AppId,
        qs: reqParams,
    }, function (err, data) {
        if (err) {
            return callback(err);
        }
        var contents = data.ListBucketResult.Contents || [];
        var CommonPrefixes = data.ListBucketResult.CommonPrefixes || [];

        contents = util.isArray(contents) ? contents : [contents];
        CommonPrefixes = util.isArray(CommonPrefixes) ? CommonPrefixes : [CommonPrefixes];

        var result = util.clone(data.ListBucketResult);
        util.extend(result, {
            Contents: contents,
            CommonPrefixes: CommonPrefixes,
            statusCode: data.statusCode,
            headers: data.headers,
        });

        callback(null, result);
    });
}

