/**
 *  Bucket  
 * @param  {Object}  params                         
 *     @param  {String}  params.Bucket              Bucket
 *     @param  {String}  params.Region              
 * @param  {Function}  callback                     
 * @return  {Object}  err                           
 * @return  {Object}  data                          
 *     @return  {Object}  data.AccessControlPolicy  
 */
function getBucketAcl(params, callback) {
    submitRequest.call(this, {
        method: 'GET',
        Bucket: params.Bucket,
        Region: params.Region,
        AppId: params.AppId,
        action: '/?acl',
    }, function (err, data) {
        if (err) {
            return callback(err);
        }
        var Owner = data.AccessControlPolicy.Owner || {};
        var Grant = data.AccessControlPolicy.AccessControlList.Grant || [];
        Grant = util.isArray(Grant) ? Grant : [Grant];
        var result = decodeAcl(data.AccessControlPolicy);
        if (data.headers && data.headers['x-cos-acl']) {
            result.ACL = data.headers['x-cos-acl'];
        }
        result = util.extend(result, {
            Owner: Owner,
            Grants: Grant,
            statusCode: data.statusCode,
            headers: data.headers,
        });
        callback(null, result);
    });
}

/**
 *  Bucket  
 * @param  {Object}  params                         
 *     @param  {String}  params.Bucket              Bucket
 *     @param  {String}  params.Region              
 *     @param  {String}  params.ACL                 privatepublic-readprivate
 *     @param  {String}  params.GrantRead           x-cos-grant-read: uin=" ",uin=" "
 *     @param  {String}  params.GrantWrite          x-cos-grant-write: uin=" ",uin=" "
 *     @param  {String}  params.GrantFullControl    x-cos-grant-full-control: uin=" ",uin=" "
 * @param  {Function}  callback                     
 * @return  {Object}  err                           
 * @return  {Object}  data                          
 */
function putBucketAcl(params, callback) {
    var headers = {};

    headers['x-cos-acl'] = params['ACL'];
    headers['x-cos-grant-read'] = params['GrantRead'];
    headers['x-cos-grant-write'] = params['GrantWrite'];
    headers['x-cos-grant-full-control'] = params['GrantFullControl'];

    var xml = '';
    if (params['AccessControlPolicy']) {
        var AccessControlPolicy = util.clone(params['AccessControlPolicy'] || {});
        var Grants = AccessControlPolicy.Grants || AccessControlPolicy.Grant;
        Grants = util.isArray(Grants) ? Grants : [Grants];
        delete AccessControlPolicy.Grant;
        delete AccessControlPolicy.Grants;
        AccessControlPolicy.AccessControlList = {Grant: Grants};
        xml = util.json2xml({AccessControlPolicy: AccessControlPolicy});
        headers['Content-MD5'] = util.binaryBase64(util.md5(xml));
        headers['Content-Type'] = 'application/xml';
    }

    submitRequest.call(this, {
        method: 'PUT',
        Bucket: params.Bucket,
        Region: params.Region,
        AppId: params.AppId,
        action: '/?acl',
        headers: headers,
        body: xml,
    }, function (err, data) {
        if (err) {
            return callback(err);
        }
        callback(null, {
            statusCode: data.statusCode,
            headers: data.headers,
        });
    });
}

/**
 *  Bucket  
 * @param  {Object}  params                         
 *     @param  {String}  params.Bucket              Bucket
 *     @param  {String}  params.Region              
 * @param  {Function}  callback                     
 * @return  {Object}  err                           
 * @return  {Object}  data                          
 *     @return  {Object}  data.CORSRules            Bucket
 */
function getBucketCors(params, callback) {
    submitRequest.call(this, {
        method: 'GET',
        Bucket: params.Bucket,
        Region: params.Region,
        AppId: params.AppId,
        action: '/?cors',
    }, function (err, data) {
        if (err) {
            if (err.statusCode === 404 && err.error && err.error.Code === 'NoSuchCORSConfiguration') {
                var result  = {
                    CORSRules: [],
                    statusCode: err.statusCode,
                };
                err.headers && (result.headers = err.headers);
                callback(null, result);
            } else {
                callback(err);
            }
            return;
        }
        var CORSConfiguration = data.CORSConfiguration || {};
        var CORSRules = CORSConfiguration.CORSRules || CORSConfiguration.CORSRule || [];
        CORSRules = util.clone(util.isArray(CORSRules) ? CORSRules : [CORSRules]);

        util.each(CORSRules, function (rule) {
            util.each(['AllowedOrigin', 'AllowedHeader', 'AllowedMethod', 'ExposeHeader'], function (key, j) {
                var sKey = key + 's';
                var val = rule[sKey] || rule[key] || [];
                delete rule[key];
                rule[sKey] = util.isArray(val) ? val : [val];
            });
        });

        callback(null, {
            CORSRules: CORSRules,
            statusCode: data.statusCode,
            headers: data.headers,
        });
    });
}

/**
 *  Bucket  
 * @param  {Object}  params                             
 *     @param  {String}  params.Bucket                  Bucket
 *     @param  {String}  params.Region                  
 *     @param  {Object}  params.CORSConfiguration       
 * @param  {Array}  params.CORSConfiguration.CORSRules  
 * @param  {Function}  callback                         
 * @return  {Object}  err                               
 * @return  {Object}  data                              
 */
function putBucketCors(params, callback) {

    var CORSConfiguration = params['CORSConfiguration'] || {};
    var CORSRules = CORSConfiguration['CORSRules'] || params['CORSRules'] || [];
    CORSRules = util.clone(util.isArray(CORSRules) ? CORSRules : [CORSRules]);
    util.each(CORSRules, function (rule) {
        util.each(['AllowedOrigin', 'AllowedHeader', 'AllowedMethod', 'ExposeHeader'], function (key, k) {
            var sKey = key + 's';
            var val = rule[sKey] || rule[key] || [];
            delete rule[sKey];
            rule[key] = util.isArray(val) ? val : [val];
        });
    });

    var xml = util.json2xml({CORSConfiguration: {CORSRule: CORSRules}});
    var headers = {};
    headers['Content-MD5'] = util.binaryBase64(util.md5(xml));
    headers['Content-Type'] = 'application/xml';

    submitRequest.call(this, {
        method: 'PUT',
        Bucket: params.Bucket,
        Region: params.Region,
        AppId: params.AppId,
        body: xml,
        action: '/?cors',
        headers: headers,
    }, function (err, data) {
        if (err) {
            return callback(err);
        }
        callback(null, {
            statusCode: data.statusCode,
            headers: data.headers,
        });
    });
}

